body {
  -webkit-font-smoothing: antialiased;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow: auto;
}

#section1 {
  background: url(/static/media/02.9dc96f20.gif) center center / cover no-repeat;
  background-color: rgba(255, 255, 255, 0.4);
  background-blend-mode: lighten;
  margin: 0px;
}
#section2 {
  background: url(/static/media/o1.98d8420f.png) center center / cover no-repeat;
  background-color: rgba(255, 255, 255, 0.4);
  background-blend-mode: lighten;
  margin: 0px;
}

section {
  position: relative;
  width: 100%;
  height: 100%;
}

.navbar {
  display: block;
  margin: 10px;
  position: fixed;
  width: 99%;
  align-items: end;
  z-index: 2;
}

button {
  padding: 10px;
  margin: 2px;
  float: right;
  font-size: 20px;
  color: white;
  background: linear-gradient(to bottom, #7d5d3b 5%, #634b30 100%);
  background-color: #7d5d3b;
  border: 1px solid #54381e;
  cursor: pointer;
  font-size: 14px;
  padding: 10px 24px;
  text-decoration: none;
}

button:hover {
  background: linear-gradient(to bottom, #634b30 5%, #7d5d3b 100%);
  background-color: #634b30;
}

.main {
  color: #3c240f;
  display: flex;
  font-family: "Playfair Display", serif;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

header {
  font-size: 10vh;
  padding: 0px;
  margin: 0px;
}

p {
  font-size: 3.5vh;
  width: 50%;
  padding: 0px;
  margin: 0px;
}

#second {
  width: 60%;
  height: 50%;
  margin: auto;
  background: #57534f;
  background-color: rgba(255, 255, 255, 0.4);
  background-blend-mode: lighten;
}

a.scroll:hover {
  opacity: 0.5;
}
a.scroll {
  position: absolute;
  bottom: 20px;
  left: 50%;
  z-index: 2;
  display: inline-block;
  transform: translate(0, -50%);
  color: #fff;
  font: normal 400 20px/1 "Josefin Sans", sans-serif;
  letter-spacing: 0.1em;
  text-decoration: none;
  transition: opacity 0.3s;
}

#section1 a {
  padding-top: 60px;
}
#section1 a span {
  position: absolute;
  top: 0;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: rotate(-45deg);
  box-sizing: border-box;
}
#section2 a {
  padding-top: 40px;
  color: #3c240f;
}
#section2 a span {
  position: absolute;
  top: 0;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #3c240f;
  border-bottom: 1px solid #3c240f;
  transform: rotate(135deg);
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.box {
  margin: 20px;
  overflow: hidden;
  max-height: 35vh;
  box-shadow: 0px 12px 18px -6px rgba(0, 0, 0, 0.3);
}

.grid {
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 30px;
  flex-basis: calc (50% - 40px);
  justify-content: center;
}

.home-container {
  display: grid;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5)),
    url(/static/media/map.cfb3cb6a.gif) center center / cover no-repeat;

  grid-template:
    "home-nav home-nav" 2%
    "activeTitle ." 2%
    "cardview calendar" 20%
    ". .       " 75%
    / 25% 75%;

  grid-gap: 10px;
  grid-row-gap: 20px;
}

.home-nav {
  grid-area: home-nav;
}

.home-active-post-title {
  grid-area: activeTitle;
  justify-self: center;
  color: #808080;
  font-family: "Montserrat", sans-serif;
  margin-top: 20px;
}

.home-cardview {
  grid-area: cardview;
  overflow: scroll;
  overflow-y: auto;
  padding: 50px;
  border: 4px solid #a9a9a9;
  border-radius: 15px;
  margin-left: 30px;
}

.home-calendar {
  grid-area: calendar;
  overflow: scroll;
  overflow-y: auto;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 40px;
  padding-top: 20px;
  justify-content: center;
  border: 4px solid #a9a9a9;
  border-radius: 15px;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
  margin-right: 30px;
}

li,
a,
button {
  font-weight: 500;
}

.home-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 10%;
  background-color: #001933;
}

.home-logo {
  cursor: pointer;
}

.home-nav-link {
  list-style: none;
}

.home-nav-link li {
  display: inline-block;
  padding: 0px 20px;
}

.home-nav-link li a {
  transition: all 0.3s ease 0s;
}

.home-nav-link li a:hover {
  color: #fffaf0;
}

.homeLogOut {
  background-color: #001933;
  border: none;
  border-radius: 50px;

  transition: all 0.3s ease 0s;
}

button:hover {
  background-color: rgba(0, 136, 169, 0.8);
}

::-webkit-scrollbar {
  display: none;
}


